<template>
  <div class="exam-detail">
    <div class="detail-header">
      <div class="back" @click="$router.back()">
        <i class="el-icon-arrow-left"></i>
        返回
      </div>
      <h2>{{ examDetail.examName }}</h2>
    </div>

    <el-card class="detail-card">
      <!-- 考试基本信息 -->
      <div class="info-section">
        <div class="info-item">
          <span class="label">考试得分：</span>
          <span class="value score" :class="{ pass: isPassed }">
            {{ examDetail.score || examDetail.currentScore || 0 }}分
          </span>
        </div>
        <div class="info-item">
          <span class="label">考试时间：</span>
          <span class="value">{{ formatTime(examDetail.submitTime) }}</span>
        </div>
        <div class="info-item">
          <span class="label">总分：</span>
          <span class="value">{{ examDetail.totalScore || 0 }}分</span>
        </div>
        <div class="info-item">
          <span class="label">及格分：</span>
          <span class="value qualify-score">{{ examDetail.qualifyScore || 0 }}分</span>
        </div>
        <div class="info-item">
          <span class="label">是否及格：</span>
          <el-tag :type="isPassed ? 'success' : 'danger'">
            {{ isPassed ? '及格' : '不及格' }}
          </el-tag>
        </div>
      </div>

      <!-- 答题详情 -->
      <div class="questions-section" v-if="examDetail.questionPapgerTopicList && examDetail.questionPapgerTopicList.length">
        <div class="section-title">答题详情</div>
        <div v-for="(question, index) in examDetail.questionPapgerTopicList" :key="question.id" class="question-item">
          <div class="question-header">
            <span class="question-index">第{{ index + 1 }}题</span>
            <span class="question-type">{{ getQuestionType(question.questionsTopic?.topicType) }}</span>
            <span class="question-score">
              得分：{{ question.isCorrect === 1 ? question.questionsTopic?.score || 0 : 0 }}分
              <span class="total-score">(总分：{{ question.questionsTopic?.score || 0 }}分)</span>
            </span>
          </div>
          <div class="question-content" v-html="question.questionsTopic?.content"></div>
          <div class="answer-section">
            <div class="your-answer">
              <span class="label">你的答案：</span>
              <span :class="{ 'correct': question.isCorrect === 1, 'wrong': question.isCorrect === 0 }">
                {{ formatAnswer(question.currentAnswer, question.questionsTopic?.questionOptionsList) }}
              </span>
            </div>
            <div class="correct-answer">
              <span class="label">正确答案：</span>
              <span>
                {{ formatCorrectAnswer(question.questionsTopic?.questionOptionsList) }}
              </span>
            </div>
          </div>
          <div class="analysis" v-if="question.questionsTopic?.analysis">
            <div class="label">解析：</div>
            <div class="content rich-text" v-html="question.questionsTopic.analysis"></div>
          </div>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
import { getExamScoreDetail } from '@/api/exam'
import logger from '@/utils/logger'

export default {
  name: 'ExamDetail',
  data() {
    return {
      examDetail: {
        examName: '',
        score: 0,
        currentScore: 0,
        submitTime: '',
        totalScore: 0,
        qualifyScore: 0,
        isPass: 0,
        questionPapgerTopicList: []
      },
      loading: false
    }
  },
  computed: {
    isPassed() {
      return (this.examDetail.isPass || 0)
    }
  },
  methods: {
    async fetchExamDetail() {
      try {
        this.loading = true
        const res = await getExamScoreDetail(this.$route.params.id)
        
        if (res.code === 200 && res.data) {
          const detail = res.data
          this.examDetail = {
            examName: detail.examName,
            score: detail.currentScore || 0,
            submitTime: detail.submitTime,
            totalScore: detail.totalScore || 0,
            qualifyScore: detail.qualifyScore || 0,
            isPass: detail.isPass,
            questionPapgerTopicList: detail.questionPapgerTopicList || []
          }
        } else {
          throw new Error(res.msg || '获取考试详情失败')
        }
      } catch (error) {
        this.$message.error(error.message || '获取考试详情失败')
        logger.error('获取考试详情失败:', error)
      } finally {
        this.loading = false
      }
    },

    // 格式化时间
    formatTime(time) {
      if (!time) return ''
      return time.split('.')[0].replace('T', ' ')
    },

    // 获取题目类型
    getQuestionType(type) {
      const typeMap = {
        '1': '单选题',
        '2': '多选题',
        '3': '判断题',
        '4': '填空题',
        '5': '简答题'
      }
      return typeMap[type] || '未知类型'
    },

    // 格式化答案
    formatAnswer(answers, options) {
      if (!answers || !options?.length) return '未作答'
      
      // 将答案ID转换为选项内容
      const answerList = answers.split(',')
      return options
        .filter(opt => answerList.includes(opt.id))
        .map(opt => opt.content)
        .join('、') || '未作答'
    },

    // 格式化正确答案
    formatCorrectAnswer(options) {
      if (!options?.length) return ''
      
      return options
        .filter(opt => opt.isRight === 1)
        .map(opt => opt.content)
        .join('、')
    }
  },
  created() {
    this.fetchExamDetail()
  }
}
</script>

<style scoped>
.exam-detail {
  padding: 20px;
  background: #f5f7fa;
  min-height: calc(100vh - 60px);
}

.detail-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.back {
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #606266;
  margin-right: 20px;
}

.back i {
  margin-right: 5px;
}

.detail-card {
  margin-bottom: 20px;
}

.info-section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-bottom: 30px;
}

.info-item {
  display: flex;
  align-items: center;
}

.label {
  color: #909399;
  margin-right: 10px;
}

.score {
  font-size: 24px;
  font-weight: bold;
  color: #f56c6c;
}

.score.pass {
  color: #67c23a;
}

.questions-section {
  margin-top: 30px;
}

.section-title {
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ebeef5;
}

.question-item {
  margin-bottom: 30px;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 4px;
}

.question-header {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.question-index {
  margin-right: 15px;
  font-weight: 500;
}

.question-type {
  margin-right: 15px;
  color: #909399;
}

.question-score {
  color: #f56c6c;
}

.total-score {
  color: #909399;
  font-size: 13px;
  margin-left: 5px;
}

.question-content {
  margin-bottom: 15px;
}

.answer-section {
  margin: 15px 0;
  padding: 15px;
  background: #fff;
  border-radius: 4px;
}

.your-answer, .correct-answer {
  margin-bottom: 10px;
}

.correct {
  color: #67c23a;
}

.wrong {
  color: #f56c6c;
}

.analysis {
  margin-top: 15px;
  padding: 15px;
  background: #fdf6ec;
  border-radius: 4px;
}

.analysis .label {
  font-weight: 500;
  color: #e6a23c;
  margin-bottom: 10px;
}

/* 添加富文本样式 */
.rich-text {
  line-height: 1.6;
}

.rich-text :deep(p) {
  margin-bottom: 8px;
}

.rich-text :deep(img) {
  max-width: 100%;
  height: auto;
  margin: 10px 0;
}

.rich-text :deep(table) {
  width: 100%;
  border-collapse: collapse;
  margin: 10px 0;
}

.rich-text :deep(table td),
.rich-text :deep(table th) {
  border: 1px solid #dcdfe6;
  padding: 8px;
}

.rich-text :deep(pre),
.rich-text :deep(code) {
  background: #f5f7fa;
  padding: 8px;
  border-radius: 4px;
  margin: 10px 0;
  font-family: monospace;
  overflow-x: auto;
}

.rich-text :deep(ul),
.rich-text :deep(ol) {
  padding-left: 20px;
  margin: 10px 0;
}

.rich-text :deep(blockquote) {
  border-left: 4px solid #e6a23c;
  margin: 10px 0;
  padding: 10px 20px;
  background: #fdf6ec;
  color: #666;
}

.qualify-score {
  font-weight: bold;
  color: #e6a23c;
}
</style>